<template>
  <div id="add-new-link">
    <div class="mdl-layout__content" style="padding: 0px 15px;">
      <h4>Add Link</h4>
    </div>
    <div class="row">
      <form @submit.prevent="saveLink" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="title" required>
            <label>Title</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" v-model="url" required>
            <label>Url (Link)</label>
          </div>
        </div>
        <button type="submit" class="btn pink">Submit</button>
        <router-link to="/dashboard" class="btn blue-grey">Cancel</router-link>
        <img v-bind:style="[ isLoading ? {'display': ''} : {'display': 'none' } ]" style="margin-left: 10px" src="../../static/loader.gif" />
      </form>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase'
export default {
  name: 'add-new-link',
  data() {
    return {
      id: null,
      title: null,
      url: null,
      isLoading: false
    }
  },
  methods: {
    saveLink () {
      this.isLoading = true
      let user = firebase.auth().currentUser
      db.collection('links').add({
        title: this.title,
        url: this.url,
        u_id: user.uid
      })
      .then(docRef => {
        this.isLoading = false
        this.$router.push('/dashboard')
      })
      .catch(err => {
        this.isLoading = false
        console.log(err)
      })
    }
  }
}
</script>

<style>
#add-new-link {
  padding: 15px 10px;
}
</style>



