<template>
  <div id="dashboard" style="height: 100%;">
    <div v-if="links.length == 0" style="background-image: url('../../static/no_data.png'); width: 100% !important; height: 100% !important; background-repeat: no-repeat; background-size: contain;">
      <router-link to="/add" class="mdl-button mdl-js-button mdl-button--fab mdl-button--colored mdl-js-ripple-effect mdl-color--pink-500" style="float: right; margin: 15px">
        <i class="material-icons">add</i>
      </router-link>
    </div>
    <div v-if="links.length > 0">
      <div  class="mdl-layout__content" style="padding: 0px 15px;">
      <h4>Saved Links</h4>
    </div>
    <!-- Colored FAB button -->
    <router-link to="/add" class="mdl-button mdl-js-button mdl-button--fab mdl-button--colored mdl-js-ripple-effect mdl-color--pink-500" style="float: right; margin: 15px">
      <i class="material-icons">add</i>
    </router-link>
    <div class="mdl-grid link-content">
      <div v-for="link in links" v-bind:key="link.id" class="mdl-cell mdl-cell--6-col">
        <div class="link-card-wide mdl-card mdl-shadow--2dp">
          <div class="mdl-card__title">
            <h2 class="mdl-card__title-text">{{link.title}}</h2>
          </div>
          <div class="mdl-card__supporting-text">
            {{link.url}}
          </div>
          <div class="mdl-card__menu">
            <router-link v-bind:to="{name: 'edit-link', params: {id: link.id}}" class="mdl-button mdl-button--icon mdl-js-button mdl-js-ripple-effect">
              <i class="material-icons">mode_edit</i>
            </router-link>
            <button @click="openInNewTab(link.url)" class="mdl-button mdl-button--icon mdl-js-button mdl-js-ripple-effect">
              <i class="fa fa-eye"></i>
            </button>
            <!--todo add share functinoality -->
            <button class="mdl-button mdl-button--icon mdl-js-button mdl-js-ripple-effect">
              <i class="fa fa-share-alt"></i>
            </button>
            <button @click="deleteDoc(link.id)" class="mdl-button mdl-button--icon mdl-js-button mdl-js-ripple-effect">
              <i class="fa fa-trash"></i>
            </button>
          </div>
        </div>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
import db from './firebaseInit'
import firebase from 'firebase'
 
export default {
  name: 'dashboard',
  data() {
    return {
      links: []
    }
  },
  created () {
    this.fetchData()
  },
  methods: {
    fetchData() {
      this.links = [];
      let user = firebase.auth().currentUser
      db.collection('links').where("u_id", "==", user.uid).get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                const data = {
                  'id': doc.id,
                  'title': doc.data().title,
                  'url': doc.data().url
                }
                this.links.push(data)
              })
            })
    },
    openInNewTab(url) {
      var win = window.open(url, '_blank');
      win.focus();
    },
    deleteDoc(id) {
      db.collection("links").doc(id).delete().then(() => {
          Materialize.toast('Successfully deleted', 2000, 'rounded')
          this.fetchData()
      }).catch((error) => {
        console.log(error)
          Materialize.toast('Unable to Delete', 2000, 'rounded')
      });

    }
  }
}
</script>


